<template>
<div class="container">
    <div class="btn-login login-text"></div>
    <div class="btn-sign  login-text" @click="navtosign"></div>
    <div class="login-phone">
        <div class="num-add">+86</div>
        <input class="login-phonenum" v-model="tel" type="number" placeholder="">
    </div>
    <div class="login-psd">
        <input class="login-psd" v-model="psd" placeholder="">
    </div>
    <div class="fogot-bar">
        <div class="choose-block" @click="changeAgreeState" :class="[tongyi?agreeicon:disagreeicon]"></div>
        <div class="agree"><span class="xieyi" @click="openXieyi"></span></div>
        <div class="fogot-btn"></div>
    </div>
    <button class="login-btn" @click="login" :class="[isready=='yes'?canlogin:cannotlogin]"></button>  
</div>

</template>
<script>
// import "../styles/vacationapply.css";
import { MessageBox } from 'mint-ui';
import { DatetimePicker } from 'mint-ui';
import { Toast } from 'mint-ui';
import { Indicator } from 'mint-ui';
import { Actionsheet } from 'mint-ui';
import axios from 'axios';
export default {
  name: "login",
  data() {
    return{
      canlogin:"can-login",
      cannotlogin:"cannot-login",
      tel:"",
      psd:"",
      isready:"no",
      tongyi:true,
      agreeicon:"agreeicon",
      disagreeicon:"disagreeicon"
    }
  },
  watch:{
      tel(val){
          if(this.tel!=""&&this.psd!=""&&this.tongyi==true){
              this.isready="yes"
          }
          else{
               this.isready="no"
               console.log("no")
               console.log(this.tel+"---"+this.psd+"---"+this.tongyi)
          }
      },
      psd(val){
        if(this.tel!=""&&this.psd!=""&&this.tongyi==true){
            this.isready="yes"
        }
        else{
            this.isready="no"
        }
      },
      tongyi(val){
        if(this.tel!=""&&this.psd!=""&&this.tongyi==true){
            this.isready="yes"
        }
        else{
            this.isready="no"
        }
      }
  },
  created(){
   
  },
  mounted(){
      //   im
    let loginInfo = {};
    loginInfo.sdkAppID = "1400068060";
    loginInfo.appIDAt3rd = "1400068060";
    loginInfo.identifier = "8447633";
    loginInfo.identifierNick = "ly";
    loginInfo.userSig = "eJxFkF1PgzAUhv8LtxrXlraAyS6wuGTiovuIRm*aBg5LdUIpHYJm-13EEW*fJyfv*55vb3e-vVLG6FwqJ32be9ce8i5HDJ3RFqQqHNgBY8YYQWiyLdhGV*UgCMIMEx*hf6lzKJ0u9HgYUhpw3z*rRu8Htrpdi6VgmjagAKFWFQHMaqhTArTdPtbBDbiXNFoskg1perGOddxfJB3b4Ifd0cbPX-hQoOVhnz0JMUsTHPLVXecMq5JX8vY5n09h*bsc1-32p0M-HiI*lXT6A-52cYojRqMzV1lWHUsnXW9gfMfpBw*aVkI_";
    loginInfo.accountType = 1

    let listeners = {
        "onConnNotify": onConnNotify//,
        ,"jsonpCallback": jsonpCallback//IE9() jsonp 
        ,"onMsgNotify": onMsgNotify//(())
        // ,"onBigGroupMsgNotify": onBigGroupMsgNotify//()
        // ,"onGroupSystemNotifys": onGroupSystemNotifys//
        // ,"onGroupInfoChangeNotify": onGroupInfoChangeNotify//
        // ,"onFriendSystemNotifys": onFriendSystemNotifys//
        // ,"onProfileSystemNotifys": onProfileSystemNotifys//
        // ,"onKickedEventCall" : onKickedEventCall//
        // ,"onC2cEventNotifys": onC2cEventNotifys// C2C 
    };
    var onConnNotify = function (resp) {
    var info;
    switch (resp.ErrorCode) {
        case webim.CONNECTION_STATUS.ON:
            webim.Log.warn(': ' + resp.ErrorInfo);
            break;
        case webim.CONNECTION_STATUS.OFF:
            info = ': ' + resp.ErrorInfo;
            alert(info);
            webim.Log.warn(info);
            break;
        case webim.CONNECTION_STATUS.RECONNECT:
            info = ': ' + resp.ErrorInfo;
            alert(info);
            webim.Log.warn(info);
            break;
        default:
            webim.Log.error(': =' + resp.ErrorInfo);
            break;
        }
    };
    function jsonpCallback(rspData) {
    // jsonp 
        webim.setJsonpLastRspData(rspData);
    }
    //
//newMsgList [Msg]
    function onMsgNotify(newMsgList) {
        //console.warn(newMsgList);
        var sess, newMsg;
        //
        var sessMap = webim.MsgStore.sessMap();
        for (var j in newMsgList) {//
            newMsg = newMsgList[j];
            if (newMsg.getSession().id() == selToID) {//
                selSess = newMsg.getSession();
                //
                //console.warn(newMsg);
                addMsg(newMsg);
            }
        }
        //
        webim.setAutoRead(selSess, true, true);
        for (var i in sessMap) {
            sess = sessMap[i];
            if (selToID != sess.id()) {//
                updateSessDiv(sess.type(), sess.id(), sess.unread());
            }
        }
    }         
    let opts = {
        isAccessFormalEnv:true,
        isLogOn:false
    }   
    let cbOk = function(resp){
        loginInfo.identifierNick = resp.identifierNick;//
        console.log(resp)
        console.log("im ")
    }
    let cbErr = function(err){
        alert(err.ErrorInfo);
    }
    setTimeout(function(){
        webim.login(loginInfo, listeners,opts,cbOk,cbErr);
    },100)

 
     
  },
  methods: {
    openXieyi(){
        window.location.href = "http://isuiliao.cn/privacy.html";
    },
    changeAgreeState(){
        this.tongyi = !this.tongyi;
    },
    login(){
        if(this.isready=="no"){
            return false;
        }else{
             this.callService({
              hash:"/suiliao-api/suiliao/sys/login",
              params:{
                phone:this.tel,
                password:this.psd,
                third_type:4,
              },
              success:(res)=>{
                console.log(res)
                if(res.err_code=="8888"){
                    debugger
                    localStorage.setItem("userInfo",JSON.stringify(res.result))
                  this.$router.push({name:"homepage", params: {}})
                }else{
                  Toast(res.err_msg)
                }
              },
              fail:function(){

              }
            }); 
        }
    },
    navtosign(){
        this.$router.push({name:"sign", params: {}})
    }
  }

};
</script>
<style  scoped lang="scss">
*{
    margin:0;
    padding:0;
}
$pink:#FF8586;
.agreeicon{
    background:url("../../assets/suiliao/zc-tyix@2x.png") no-repeat center;
    background-size:14px;
}
.disagreeicon{
    background:url("../../assets/suiliao/zc-tyi@2x.png") no-repeat center;
    background-size:14px;
}
.login-btn{
    width:90%;
    margin:0 auto;
    height:42px;
    line-height:42px;
    font-size:14px;
    display: block;
    border:none;
    margin-top:30px;
    outline: none;
}
.can-login{
    background:$pink;
    color:#fff;
}
.cannot-login{
    background:#eee;
    color:#fff;
}
.fogot-bar{
    height:20px;
    display:flex;
    font-size:12px;
    width:90%;
    margin:0 auto;
    .choose-block{
        width:20px;
        height:20px;
        margin-top:-1px;
        margin-right:3px;
    }
    .agree{
        color:#999;
        font-size:12px;
        flex:1;
        span{
            color:$pink;
        }
    }
    .fogot-btn{
        color:$pink;
    }
}
.container{
    width:100vw;
    height:100vh;
    background:#fff;
    position: absolute;
    z-index:9999;
    overflow: hidden;
}
.login-text{
    font-size:20px;
    font-weight: bold;
    padding-left:20px;
    height:20px;
    line-height:20px;
}
.login-phone{
    width:90%;
    margin:30px auto 20px auto;
    border-bottom: 1px solid #E3E3E3;
    display:flex;
    height:40px;
    font-size:14px;
    .num-add{
        width:40px;
        line-height: 40px;
    }
    .login-phonenum{
        outline: none;
        flex:1;
        line-height:40px;
        font-size:14px;
        border:none;
    }
}
.login-psd{
    width:90%;
    margin:0 auto 20px auto;
    border-bottom: 1px solid #E3E3E3;
    display:flex;
    height:40px;
    font-size:14px;
    display:flex;
    .login-psd{
        outline: none;
        flex:1;
        line-height:40px;
        font-size:14px;
        border:none;
        height:38px;
    }
}
.btn-login{
    padding-top:50px;
}
.btn-sign{
    font-weight: 100;
    padding-top:20px;
}
.clear{
  clear:both;
}

</style>


